            //               {/* content image */}
            //               <div className="feed-images pointer">
            //               <Image.PreviewGroup>  
            //                    <Carousel>
                                 
            //                   {
            //                     this.state.feedMedia.length !== 0?

            //                     this.state.feedMedia.map((element,i)=>{
            //                       return(
            //                        <>
            //                        {
            //                         element.feedMedia_type === "image/jpeg" || element.feedMedia_type === "image/jpg" || element.feedMedia_type === "image/png"?
