import React from 'react'
import ProfileImage from './profileImage/profileImage'
import AllstoreTemp from './AllstoresTemp'
import Helmet from '../../Helmet/helment'
class AllStores extends React.Component{
 
    render(){
        return(
            <div>
                <Helmet title={'Store'} description={'Index of all pages rendered on palscheck '}/>
                {/* profileimage */}
                <div className="profile-cover  relative">
                    <ProfileImage/>
                </div>
                <div className="">
                    <AllstoreTemp/>
                </div>
            </div>
        )
    }
}
export default AllStores