import React from 'react'
import { Select } from 'antd';
import TextField from '@material-ui/core/TextField';
import {
    CameraOutlined,} from '@ant-design/icons'
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import { Alert,Button,notification } from 'antd';

const { Option } = Select;
const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;

const ActivatePaywall = ()=>{
    const [selectedValue, setSelectedValue] = React.useState('');
    const [IDnum, setIDnum] = React.useState('');
    const [IDonly, setPicture1] = React.useState(null);
    const [eventIDonly, setImgData1] = React.useState('https://cdn.wallpapersafari.com/25/14/Dljuh1.jpg');
    const [IDAU, setPicture2] = React.useState(null);
    const [eventIDAU, setImgData2] = React.useState('https://cdn.wallpapersafari.com/25/14/Dljuh1.jpg');
    const [load,setload] = React.useState(false)
    const [error,setError] = React.useState(false)
    
    function onChangeIDTypte(value) {
        setSelectedValue(value);
    }

    //notification
    const openNotification = (type,desc) => {
        notification[type]({
            message: 'Success',
            description:desc
            
          });
        };
    //changing image1
    const setIDIMAGE1 = e => {
        if (e.target.files[0]) {
            setPicture1(e.target.files[0]);
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                setImgData1(reader.result);
            });
            reader.readAsDataURL(e.target.files[0]);
        }
    };
    //changing image2
    const setIDIMAGE2 = e => {
        if (e.target.files[0]) {
            setPicture2(e.target.files[0]);
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                setImgData2(reader.result);
            });
            reader.readAsDataURL(e.target.files[0]);
        }
    };

    const onFinish = async() => {
        setload(true)
        const formData = new FormData()
        formData.set('IdType',selectedValue)
        formData.set('iDnum',IDnum)
        formData.append('files',IDonly)
        formData.append('files',IDAU)
        const SendDAta = await fetch('http://localhost:4000/PayWall/CreatePayAct',{
            method:'POST',
            headers:{token:localStorage.token},
            body:formData
        })
        const res = await SendDAta.json()
        if(res.error){
            setload('')
            setError(res.error)
        }else if(res.success){
            setload('')
            setError('')
            openNotification('success',res.success)
        }
    }
    return (
        <div>
            <h3 className="center tc light-blue">let's activate your paywall</h3>

            <div className="pai">
                <Alert
                    message="Informational Notes"
                    description="Please make sure to complete the follwing process for approval this helps us to
      identify that this account belongs to you and help protect your information from unauthorized users"
                    type="info"
                    showIcon
                />
            </div>
            <div className="pai_1ty mt4">
                { /* identity TYPE */}
                <div className="center">
                    <h3 className="tc light-blue">Upload your legal info and use any valid government issued ID</h3>
                    <span className="f6 gray small dim">Note: this info won't be displayed on your public profile and cannot be changed</span>
                    <span>
                        <Select
                            style={{ width: 400 }}
                            placeholder="Select Identification type"
                            optionFilterProp="children"
                            onChange={onChangeIDTypte}
                            filterOption={(input, option) =>
                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                            }
                        >
                            <Option value="idcard">VALID IDCARD</Option>
                            <Option value="driver licence">VALID DRIVER LICENCE</Option>
                            <Option value="ssc">SOCIAL SECURITY CARD</Option>
                            <Option value="passport">PASSPORT</Option>
                        </Select>
                    </span>
                    {/* SET ID NUMBER */}
                    <div className="pai_2in mt2 pa1 ph2">
                        <span className="db">ID number</span>
                        <TextField onChange={e=>setIDnum(e.target.value)} value={IDnum} className="w-80 center" id="outlined-basic" label="ID number" variant="outlined" />
                    </div>

                    {/* set ID IMAGE */}
                    <div className="pai_3im mt2 pa1 ph2">
                        <div className="pai_ex">
                            <span className="f6 gray dim sm">Note: please make sure the info on your ID is clear enough.</span>
                            <img src="https://support.tenx.tech/hc/article_attachments/360012551992/IDverification_final.png" alt="" />
                        </div>
                        <div className="w-100 pa2 ph2 br3 center edit_cover_img ba b--blue">
                            <span>ID CARD ONLY</span>
                            <div className="editinnerCoverImage">
                                <img src={eventIDonly} alt="" />
                            </div>
                            <label className="f3 pointer">
                                <input type="file" style={{ display: 'none' }} onChange={setIDIMAGE1} />
                                <CameraOutlined />
                            </label>
                        </div>
                        <div className="w-100 pa2 ph2 br3 center edit_cover_img ba b--blue">
                            <span>User holding ID CARD</span>
                            <div className="editinnerCoverImage">
                                <img src={eventIDAU} alt="" />
                            </div>
                            <label className="f3 pointer">
                                <input type="file" style={{ display: 'none' }} onChange={setIDIMAGE2} />
                                <CameraOutlined />
                            </label>
                        </div>
                        {
                            error.length > 0?
                            <Alert message={error} type="error" showIcon />
                            :null
                        }
                        <a class="ml4 mt1 f5 link br-pill bg-light-blue ph3 pv2 w-30 dib fw6 white dib tc" onClick={onFinish}>Approve {load === true? <Spin indicator={antIcon} />:null}</a>
                    </div>
                </div>
            </div>
            {/* post Identity number */}

            {/* post identy ID Image */}
            {/* post user Holding IDentity */}
        </div>
    )
}
export default ActivatePaywall