import React, { useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import {Image} from 'antd'
import { CloseCircleOutlined,CaretDownOutlined} from '@ant-design/icons';
import TableChartIcon from '@material-ui/icons/TableChart';
import SupervisedUserCircleIcon from '@material-ui/icons/SupervisedUserCircle';
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';
import 'emoji-mart/css/emoji-mart.css'
import ContentEditable from 'react-contenteditable'
import { Popover, Button } from 'antd';
import PublicIcon from '@material-ui/icons/Public';
import PollChoice from './pollchoice'
import { Spin,Alert } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';

const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;


const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        '& > *': {
            margin: theme.spacing(1),
        },
    },
}));
const UploadTestComponent = () => {
    const classes = useStyles();
    const [image, setimage] = React.useState([])
    const [Imageset,setImageset] = React.useState([])
    const [innerTxt, setInnerTxt] = React.useState('')
    const [postFor,setPostFor] = React.useState('followers')
    const [pollroute, setPollroute] = React.useState('facematch')
    const [pollChoice,setPollChoice] = React.useState([])
    const [load,setload] = React.useState(false)
    const [error,setError] = React.useState('')
    const [success,setSuccess] = React.useState('')

    //Update pollroute

    const UpdatePollRoute = (route) =>{
        setPollroute(route)
    }
  
    // image and video configuration
    const previewImages = (event)=> {

        var preview = document.querySelector('#preview');

        if (event.target.files) {
            [].forEach.call(event.target.files, readAndPreview);
            setImageset(Imageset => [event.target.files, ...Imageset])
        }

        function readAndPreview(file) {
            var reader = new FileReader();
            // Make sure `file.name` matches our extensions criteria
            if (/\.(jpe?g|png|gif)$/i.test(file.name)) {

                reader.addEventListener("load", function () {

                    setimage(image => [this.result, ...image])
                });

                reader.readAsDataURL(file);
            } else {
                return alert(file.name + " is not a gallery file");
            } // else...
        }
    }

    const removeImage = (name) => {
        setimage(image.filter(item => item !== name));
    };


    const setChoices = (data) =>{
        setPollChoice(data)
    }
    const handleChange = evt => {
        setInnerTxt(evt.target.value);
    };

    const onFinish = async()=>{
            setload(true)
            const formData = new FormData()
            formData.set('text',innerTxt)
            if(pollroute === 'vote'){
                formData.set('pollchoice',pollChoice)
            } else if (pollroute === 'facematch') {
                Imageset.forEach((element, i) => {
                formData.append('files',element[i])
            });
            }          
            const sendData = await fetch('http://localhost:4000/Feed/create/Poll',{
            method:'POST',    
            headers:{token:localStorage.token},
            body:formData
            })
            const res = sendData.json()
            if(res.data){
                setload(false)
                setSuccess(res.data)
                setError('')
            }else if(res.error){
                setload(false)
                setError(res.error)
                setSuccess('')
            }
        }
    

// who can see your post
const content = (
    <div className="f5 fw6 blue">
        <h3 className="w-80 tc fw6 b blue">choose where your post should be seen, by defult only users following you can see this.</h3>
      <p className="flex blue pa2 ph1 hover-bg-lightest-blue w-30 br-pill pointer" onClick={e=>setPostFor('public')}><PublicIcon/>Public</p>
      <p className="flex blue pa2 ph1 hover-bg-lightest-blue w-30 br-pill pointer" onClick={e=>setPostFor('followers')}><SupervisedUserCircleIcon/>Followers</p>
    </div>
  );
    return (
        <div className=" br3 w-100">
            <div className="w-100">
                {/* user profile details */}
                {
                    pollroute === 'vote'?
                    <h2 className="pa2 ph2 fw5 pa3 f5 ml3 mb3">start a poll, allow users vote between different options</h2>
                    :pollroute === 'facematch'?
                    <h2 className="pa2 ph2 fw5 pa3 f5 ml3 mb3">start a facematch and allow users vote determine the winner</h2>
                    :null
                }
               
                    {
                        success.length > 0?
                        <Alert
                        type="success"
                        message={success}
                        showIcon
                        />
                        :error.length > 0?
                        <Alert
                        type="error"
                        message={error}
                        showIcon
                        />:null
                    }
                <div className={classes.root}>
                    <div className="Pas3lserInfo" >
                        <Avatar alt="Heldt h" src="/static/images/avatar/1.jpg" />
                    </div>
                    <div className="Pas3ls_content w-100">
                        {/* <div onInput={e=>OnchangeTxt(e)} className="w-100 Pas3ls_2ontent f3 gray pa2 ph2" contenteditable="true" placeholder="Enter text here...">{innerTxt}</div> */}
                        <ContentEditable html={innerTxt} className="w-100 Pas3ls_2ontent f3 gray pa2 ph2 fw5" onChange={handleChange} placeholder="Ask question..." />
                    {
                            pollroute === 'vote' ?
                                <div className="ba b--black-10 br4 w-90 center pa2">
                                    <PollChoice upDateChoice={setChoices}/>
                                </div>
                        :null
                    }
                    
                    </div>
                </div>

                <div className=" bt b--black-10 ">
                    {/* pick where your post should be seen */}
                        <div className="flex blue">
                        <Popover content={content} trigger="click" placement="bottom">
                            <div className="flex blue items-center ml5 pa1 pointer hover-bg-lightest-blue w-auto mt2 br-pill">
                                <PublicIcon />who can vote on this? <span className="ml5"><CaretDownOutlined />{postFor}</span>
                    </div>
                        </Popover>
                        </div>
                    <div className="flex items-center ml5">
                        {/* <label class="pa2 ph2 hover-bg-lightest-blue pointer w-auto br-pill flex f3 fw6 blue">

                            <TableChartIcon onClick={e=>UpdatePollRoute('vote')}/>


                        </label> */}
                        <label class="pa2 ph2 hover-bg-light-pink pointer w-auto br-pill flex f3 fw6 blue">
                            <input onChange={e=>previewImages(e)} id="file-input" type="file" multiple style={{ display: 'none' }} />
                            <AddAPhotoIcon onClick={e=>UpdatePollRoute('facematch')}/>

                        </label>
                        <a class="ml4 mt1 f5 link br-pill bg-light-blue ph3 pv2 dib fw6 white dib tc" onClick={onFinish}>upload {load === true? <Spin indicator={antIcon} />:null}</a>
                        
                    </div>
                </div>

                <div>
                    
                            <div id="preview">
                                {
                                    image.length > 0 ?
                                        image.map((srcs, i) => {
                                            return (
                                                <section className="preview_div">
                                                    <span className="pointer f3 blue" onClick={e => removeImage(srcs)}><CloseCircleOutlined /></span>
                                                    <Image src={srcs} />
                                                </section>
                                            )
                                        })
                                        :
                                        null
                                }

                            </div>
                </div>
            </div>

        </div>
    )
}
export default UploadTestComponent