import React, { useEffect, useState } from 'react'
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import Flutter from './flutter'

const localLink = 'http://localhost:4000'
const SeverLink = 'https://still-cover-backend.uc.r.appspot.com'

const Payment = () =>{
const [route,setRoute] = useState(0)
const [payWallDT,setPayWallDt] = useState([])

useEffect(()=>{
    setInterval(() => {
        getPayWallDetails()
    }, 1000);
})
const getPayWallDetails = async() =>{
    const Fetchall = await fetch(`${localLink}/Authentication/fetchAll/PaywallDetailsOFuser`,{
        headers:{token:localStorage.token},
    })
    const res = await Fetchall.json()
    if(res.data){
        setPayWallDt(res.data)
    }
}

return(
        <div>
            <div className="fname">
                <h1 className="center blue ttu tc b f1 pointer">Palscheck pay</h1>
                <div className="route flex ba br3 b--black-10">
                    <div className="route_list w-30 br b--black-10">
                        <li className="ttc f5 fname list pa2 br-pill hover-bg-light-blue pointer w-auto bb tc mb3" onClick={e=>setRoute(1)}>Top my account</li>
                        <li className="ttc f5 fname list pa2 br-pill hover-bg-light-blue pointer w-auto bb tc mb3">fund user</li>
                        <li className="ttc f5 fname list pa2 br-pill hover-bg-light-blue pointer w-auto bb tc mb3">Donate</li>
                        <li className="ttc f5 fname list pa2 br-pill hover-bg-light-blue pointer w-auto bb tc mb3">History</li>
                        <li className="ttc f5 fname list pa2 br-pill hover-bg-light-blue pointer w-auto bb tc mb3">Activity</li>
                    </div>
                    <div className="route_view w-50 center">
                        {
                            route === 0 ?
                                <div className="center tc">
                                    <p className="fname f3 tc">we make it easy to process payment on palschek</p>
                                <img className="w-60 center" src="https://htxt.co.za/wp-content/uploads/2019/03/standard-bank-virtual-card.jpg" alt=""/>
                                </div>:
                                route === 1 ?
                                   <div className="center tc">
                                       <h3 classname="f3 tc">Top up my account</h3>
                                    {/* pay with flutter wave or other paymant gateway */}
                                    <Flutter/>
                                    <div className="mt4">
                                        <img src="https://www.nicepng.com/png/detail/207-2076847_paypal-acceptance-mark-major-credit-card-logos-png.png" alt=""/>
                                    </div>
                                   </div>
                                : null
                        }
                    </div>
                    <div className="route_add  w-20 fname bl b--black-10">
                        <div className="tc pa2">
                            <span>your wallet balance is</span>
                            <span className="db center tc"><AttachMoneyIcon/>10.5</span>
                        </div>
                    </div>
                </div>
            </div>
           
        </div>
    )
}
export default Payment