import React from 'react'
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import AccountBalanceWalletSharpIcon from '@material-ui/icons/AccountBalanceWalletSharp';
import ActivatePay from './activateP'
import { Alert} from 'antd';
import CircularProgress from '@material-ui/core/CircularProgress';
import AddBank from './addBank'

const Paywall = ({PaywallData}) => {
    const [route,setRoute] = React.useState(0)
    
    React.useEffect(()=>{
    })
    const setroute = (e)=>{
        setRoute(e)
    }

    return (
        <div className="flex">
            {/* menu */}
            <div className="w-40 br b--black-10">
                {
                    PaywallData ?
                        PaywallData.status === 'pending' ?
                            <Alert
                                message="Informational Notes"
                                description="Your details where submitted for review we will get back to you soon."
                                type="info"
                                showIcon
                            />
                            : PaywallData.status === 'approved' ?
                                <li className="bb b--black-10 pa2 tj list link f5 pointer black flex" onClick={e => setroute(1)}><AccountBalanceIcon />Add bank info</li>
                                :
                                <li className="bb b--black-10 pa2 tj list link f5 pointer black flex" onClick={e => setroute(2)}><AccountBalanceWalletSharpIcon />Activate paywall</li>
                        :
                        <CircularProgress />
                }


            </div>
            {/* result */}
            <div className="w-60 bl b--black-10">
                {route === 1 ? <AddBank/> : route === 2 ? <ActivatePay /> : null}
            </div>
        </div>

    )
}
export default Paywall