import React, { useState,useEffect } from 'react' 
import { UserOutlined,ShopOutlined,HeartFilled,FundFilled } from '@ant-design/icons';
import InsertCommentIcon from '@material-ui/icons/InsertComment';
import { Link } from 'react-router-dom';
import RandomString from 'randomstring'
import Avatar from '../Dashboard/Defaults/defaultImage'
import CommentIcon from '@material-ui/icons/Comment';
import ReplyAllIcon from '@material-ui/icons/ReplyAll';
import SupervisorAccountIcon from '@material-ui/icons/SupervisorAccount';
import FavoriteIcon from '@material-ui/icons/Favorite';
import SubscriptionsIcon from '@material-ui/icons/Subscriptions';
import MoneyOffIcon from '@material-ui/icons/MoneyOff';
import MonetizationOnIcon from '@material-ui/icons/MonetizationOn';
import PalcheckLogo from '../Logo/palscheck-logo/pals-check-blue-favicon.png'
import FeedView from '../Newsfeed/Newsfeed/newsFeedTemp'
const Randstring  = RandomString.generate({
  length:50
})

const localLink = 'http://localhost:4000'
const SeverLink = 'https://still-cover-backend.uc.r.appspot.com'

const NotificationTemp = ({addressFrom,notify_action,notify_content,date,id,notify_from,notify_for,viewed})=>{
  const [userInfo,setUserInfo] = useState([])
  const [userInfoFor,setUserInfoFor] = useState([])
  const [view,setview] = useState(0)
  const [FeedInfo,setFeedInfo]= useState([])
  useEffect(()=>{
    UserDetails()
    UserDetailsFor()
    GetSinglePost()
  })

  //GEt all user additional info

  const UserDetails = async () => {
    if (notify_from === 'palscheck') {
      return (null)
    } else {
      const FetchAllDetails = await fetch(`${SeverLink}/Authentication/by_id/${notify_from}`)
      const response = await FetchAllDetails.json()
      
      if (response.profiler) {
        setUserInfo(response.profiler)
      }
    }

  }
  const UserDetailsFor = async () => {
    if (notify_from === 'palscheck') {
      return (null)
    } else {
      const FetchAllDetails = await fetch(`${SeverLink}/Authentication/by_id/${notify_for}`)
      const response = await FetchAllDetails.json()
      
      if (response.profiler) {
        setUserInfoFor(response.profiler)
      }
    }

  }
  const UpdateNotify = async(address)=>{
    const UpdateN = await fetch(`${SeverLink}/Authentication/update/Notify/${address}`,{
      method:"PATCH"
    })
    const response = await UpdateN.json()
    if(response.result){
      setview(1)
    }
  }
   // convey Time Frame
   const time_ago = (time) =>{

    switch (typeof time) {
      case 'number':
        break;
      case 'string':
        time = +new Date(time);
        break;
      case 'object':
        if (time.constructor === Date) time = time.getTime();
        break;
      default:
        time = +new Date();
    }
    var time_formats = [
      [60, 'seconds', 1], // 60
      [120, '1 minute ago', '1 minute from now'], // 60*2
      [3600, 'minutes', 60], // 60*60, 60
      [7200, '1 hour ago', '1 hour from now'], // 60*60*2
      [86400, 'hours', 3600], // 60*60*24, 60*60
      [172800, 'Yesterday', 'Tomorrow'], // 60*60*24*2
      [604800, 'days', 86400], // 60*60*24*7, 60*60*24
      [1209600, 'Last week', 'Next week'], // 60*60*24*7*4*2
      [2419200, 'weeks', 604800], // 60*60*24*7*4, 60*60*24*7
      [4838400, 'Last month', 'Next month'], // 60*60*24*7*4*2
      [29030400, 'months', 2419200], // 60*60*24*7*4*12, 60*60*24*7*4
      [58060800, 'Last year', 'Next year'], // 60*60*24*7*4*12*2
      [2903040000, 'years', 29030400], // 60*60*24*7*4*12*100, 60*60*24*7*4*12
      [5806080000, 'Last century', 'Next century'], // 60*60*24*7*4*12*100*2
      [58060800000, 'centuries', 2903040000] // 60*60*24*7*4*12*100*20, 60*60*24*7*4*12*100
    ];
    var seconds = (+new Date() - time) / 1000,
      token = 'ago',
      list_choice = 1;
  
    if (seconds === 0) {
      return 'Just now'
    }
    if (seconds < 0) {
      seconds = Math.abs(seconds);
      token = 'from now';
      list_choice = 2;
    }
    var i = 0,
      format;
    while (format = time_formats[i++])
      if (seconds < format[0]) {
        if (typeof format[2] == 'string')
          return format[list_choice];
        else
          return Math.floor(seconds / format[2]) + ' ' + format[1] + ' ' + token;
      }
    return time;
  }
  
  const DisplayIcon = (event)=>{
    if(event === 'following'){
      return(<SupervisorAccountIcon/>)
    }else if(event === 'reaction'){
      return(<FavoriteIcon/>)
    }else if(event === 'comment'){
      return(<CommentIcon/>)
    }else if(event === 'subme'){
      return(<SubscriptionsIcon/>)
    }else if(event === 'submeNotice'){
      return(<SubscriptionsIcon/>)
    }else if(event === 'acctbalance'){
      return(<MoneyOffIcon/>)
    }
    
  }

  const GetSinglePost = async()=>{
    const FetchData = await fetch(`${localLink}/Feed/single/${addressFrom}`)
    const response = await FetchData.json()
    if(response.result){
      setFeedInfo(response.result)
    }
  }

  
  return (
    
    <div>
        {
          notify_action === 'following' ?
            // {/* notification from users action following*/}
            <article class="center br3 hidden ba b--black-10 mv4">
           <div class="pa1 bt b--black-10">
           <Link to={`${userInfo.username}.pal`}>
              <div className="mr2">
              <Avatar name={userInfo.fullname} size={40} src={userInfo.profileImg}/>
                <div className="dib ml2 fw6">{userInfo.fullname} &nbsp;.&nbsp;<div className="fw1 gray sm dim f6 dib">{notify_action}</div></div>
                </div>
              <p class="f6 lh-copy fname db ml4">
              {notify_content}
              </p>
              <div className="ml4 blue tr flex">
                {DisplayIcon(notify_action)}{time_ago(new Date(date))}
              </div>
              </Link>
              <div className="tr light-blue pointer"><ReplyAllIcon onClick={e=>UpdateNotify(id)}/>Archive</div>
            </div>
          </article>
            : notify_action === 'comment' ?
            <article class="center br3 hidden ba b--black-10 mv4">
            <div class="pa1 bt b--black-10">
               <div className="mr2">
               <Link to={`${userInfo.username}.pal`}>
               <Avatar name={userInfo.fullname} size={40} src={userInfo.profileImg}/>
                 <div className="dib ml2 fw6">{userInfo.fullname} &nbsp;.&nbsp;<div className="fw1 gray sm dim f6 dib">{notify_action}</div></div>
               </Link>
                 </div>
               <p class="f6 lh-copy fname db ml4">
               {notify_content}
               </p>
               <div className="ml4 blue tr flex">
                 {DisplayIcon(notify_action)}{time_ago(new Date(date))}
               </div>
               {/* //Display Feed Content */}
               <div className="center b--black-10">
                 <hr/>
                <div className="w-50 center">
                <FeedView
               route={'notification'}
               feedby={FeedInfo.feedby} id={FeedInfo._id} feedFor={FeedInfo.feedFor} feedType={FeedInfo.feedType} feedTxt={FeedInfo.feedTxt} address={FeedInfo.address} date={FeedInfo.date}
               />
                </div>
               </div>
               <div className="tr light-blue pointer"><ReplyAllIcon onClick={e=>UpdateNotify(id)}/>Archive</div>
             </div>
           </article>
              : notify_action === 'reaction' ?
                // {/* notification from users action react*/}
                <article class="center br3 hidden ba b--black-10 mv4">
           <div class="pa1 bt b--black-10">
              <div className="mr2">
              <Link to={`${userInfo.username}.pal`}>
              <Avatar name={userInfo.fullname} size={40} src={userInfo.profileImg}/>
                <div className="dib ml2 fw6">{userInfo.fullname} &nbsp;.&nbsp;<div className="fw1 gray sm dim f6 dib">{notify_action}</div></div>
              </Link>
                </div>
              <p class="f6 lh-copy fname db ml4">
              {notify_content}
              </p>
              <div className="ml4 dark-red tr flex">
                {DisplayIcon(notify_action)}{time_ago(new Date(date))}
              </div>
              {/* //Display Feed Content */}
              <div className="center b--black-10">
                 <hr/>
                <div className="w-50 center">
                <FeedView
               route={'notification'}
               feedby={FeedInfo.feedby} id={FeedInfo._id} feedFor={FeedInfo.feedFor} feedType={FeedInfo.feedType} feedTxt={FeedInfo.feedTxt} address={FeedInfo.address} date={FeedInfo.date}
               />
                </div>
               </div>
              <div className="tr light-blue pointer"><ReplyAllIcon onClick={e=>UpdateNotify(id)}/>Archive</div>
            </div>
          </article>
          : notify_action === 'subme' ?
          // {/* notification from users action react*/}
          <article class="center br3 hidden ba b--black-10 mv4">
     <div class="pa1 bt b--black-10">
        <div className="mr2">
        <Link to={`${userInfo.username}.pal`}>
        <Avatar name={userInfo.fullname} size={40} src={userInfo.profileImg}/>
          <div className="dib ml2 fw6">{userInfo.fullname} &nbsp;.&nbsp;<div className="fw1 gray sm dim f6 dib">{notify_action}</div></div>
        </Link>
          </div>
        <p class="f6 lh-copy fname db ml4">
        {notify_content}
        </p>
        <div className="ml4 mid-gray tr flex">
          {DisplayIcon(notify_action)}{time_ago(new Date(date))}
        </div>
        
        <div className="tr light-blue pointer"><ReplyAllIcon onClick={e=>UpdateNotify(id)}/>Archive</div>
      </div>
    </article>
    : notify_action === 'submeNotice' ?
    // {/* notification for subscription*/}
    <article class="center br3 hidden ba b--black-10 mv4">
<div class="pa1 bt b--black-10">
  <div className="mr2">
    {
      notify_from === "palscheck"?
   <>
    <Avatar name={'palscheck'} size={40} src={PalcheckLogo}/>
    <div className="dib ml2 fw6">{'palscheck'} &nbsp;.&nbsp;<div className="fw1 gray sm dim f6 dib">{notify_action}</div></div>
    
   </>
      :null
    }
    </div>
  <p class="f6 lh-copy fname db ml4">
  {notify_content}
  </p>
  <div className="ml4 mid-gray tr flex">
    {DisplayIcon(notify_action)}{time_ago(new Date(date))}
  </div>
  
  <div className="tr light-blue pointer"><ReplyAllIcon onClick={e=>UpdateNotify(id)}/>Archive</div>
</div>
</article>
: notify_action === 'acctbalance' ?
// {/* notification from users action react*/}
<article class="center br3 hidden ba b--black-10">
<div class="pa1 bt b--black-10">
<div className="mr2">
{
  notify_from === "palscheck"?
<>
<Avatar name={'palscheck'} size={40} src={PalcheckLogo}/>
<div className="dib ml2 fw6">{'palscheck'} &nbsp;.&nbsp;<div className="fw1 gray sm dim f6 dib">{notify_action}</div></div>

</>
  :null
}
</div>
<p class="f6 lh-copy fname db ml4">
{notify_content}
</p>
<div className="ml4 mid-gray tr flex">
{DisplayIcon(notify_action)}{time_ago(new Date(date))}
</div>

<div className="tr light-blue pointer"><ReplyAllIcon onClick={e=>UpdateNotify(id)}/>Archive</div>
</div>
</article>
                   : notify_action === 'profileImg' ?
                   // {/* notification from users action love*/}
                   view === 0 && viewed === 0?
                   <li className="bb b--black-10 bg-light-blue">
                         <div class="dib ml2 ">
                           <p class="lh-copy">{notify_content}</p>
                           <span className="tl blue f4 mr1"><UserOutlined /></span>
                           <span className="tl gray mr1">{time_ago(new Date(date))}</span>
                         </div>
                         {
                           viewed===0?
                           <span className="pointer bg-white ba b--black-10 tr grow" onClick={e=>UpdateNotify(id)}>
                             mark as read
                           </span>
                           :
                          null
                         }
                   </li>
                   :
                   <li className="bb b--black-10">
                         <div class="dib ml2 ">
                           <p class="lh-copy">{notify_content}</p>
                           <span className="tl blue f4 mr1"><UserOutlined /></span>
                           <span className="tl gray mr1">{time_ago(new Date(date))}</span>
                         </div>
                   </li>
                   
                   :notify_action === 'create-page' ?
                    // {/* notification for creating page successfully*/}
                    view === 0 && viewed === 0?
                    <li className="bb b--black-10 bg-light-blue">
                          <div class="dib ml2 ">
                            <p class="lh-copy">{notify_content}</p>
                            <span className="tl blue f4 mr1"><ShopOutlined /></span>
                            <span className="tl gray mr1">{time_ago(new Date(date))}</span>
                          </div>
                          {
                            viewed===0?
                            <span className="pointer bg-white ba b--black-10 tr grow" onClick={e=>UpdateNotify(id)}>
                              mark as read
                            </span>
                            :
                           null
                          }
                    </li>
                    :
                    <li className="bb b--black-10">
                          <div class="dib ml2 ">
                            <p class="lh-copy">{notify_content}</p>
                            <span className="tl blue f4 mr1"><ShopOutlined /></span>
                            <span className="tl gray mr1">{time_ago(new Date(date))}</span>
                          </div>
                    </li>
                : null
        }

    </div>

  )
}
export default NotificationTemp