import React, { useEffect } from 'react'
import { Row, Col,Select,Button} from 'antd';
import { DatePicker} from 'antd';
import { TimePicker } from 'antd';
import { Radio,Alert } from 'antd';
import {
    InfoCircleOutlined,CameraOutlined
    
  } from '@ant-design/icons';
import { Spin } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';

const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;



const TestComponent = () =>{
    const [EvType, setEvType] = React.useState('');
    const [title, setTitle] = React.useState('');
    const [desc, setDesc] = React.useState('');
    const [time, setTime] = React.useState('');
    const [date, setDate] = React.useState('');
    const [location, setlocation] = React.useState([]);
    const [Picture, setPicture] = React.useState('https://www.crucial.com.au/blog/wp-content/uploads/2014/12/events_medium.jpg');
    const [eventCover, setImgData] = React.useState([]);
    const [error, setError] = React.useState([])
    const [success, setSuccess] = React.useState([])
    const [load,setload] = React.useState(false)
    //changing event cover art
    const onChangePicture = e => {
        if (e.target.files[0]) {
          setPicture(e.target.files[0]);
          const reader = new FileReader();
          reader.addEventListener("load", () => {
            setImgData(reader.result);
          });
          reader.readAsDataURL(e.target.files[0]);
        }
      };

    //set time of event
    function onChangeTime(time, timeString) {
        setTime(timeString)
    }

    // set type of event
    const onChangeRadio = e => {
        setEvType(e.target.value);
    };
    //  event title
    const onChangeTitle = e => {
        setTitle(e.target.value);
    };

    //set Date of event
    const onChangeDate = (e) => {
        setDate(e._d.toDateString());
    };

    //set Location
    const onChangeLocation = e => {
        setlocation(e.target.value);
    };

    // set description of event
    const onChangeDesc = e => {
        setDesc(e.target.value);
    };
    
   
    //onFinish filling event details
    const onFinish = async() =>{
       if(title !== ''&& desc !== '' && EvType !== '' && date !== '' && time !== '' && location !== ''){
        setError('')
        setload(true)
        const formData = new FormData()
        formData.set('eventtitle',title)
        formData.set('eventdesc',desc)
        formData.set('eventtype',EvType)
        formData.set('eventDate',date)
        formData.set('eventtime',time)
        formData.set('eventlocation',location)
        formData.append('file',Picture)
        const createEvent = await fetch('http://localhost:4000/Feed/create/Event',{
            method: 'POST',
            headers:{token:localStorage.token},
            body:formData
        })
        const res = await createEvent.json()
        if(res.data){
            setSuccess(res.data)
            setError('')
            setload(false)
        }else if(res.error){
            setSuccess('')
            setError(res.error)
            setload(false)
        }
       }else{
           setError('check for missing data')
           setSuccess('')
       }
    }

    return (
        <div>
            {/* title */}
            <header className="tc">
                <h1>Create Event</h1>
            </header>
            <div className="db w-50">
                {success.length >0?<Alert message={'success'} type="success" description={`${success}`} showIcon/>
                :error.length > 0?
                <Alert message={'error'} type="error" description={`${error}`} showIcon/>
                :null
    }
            </div>
            <div className="flex w-100 center">
                {/* Detail input section */}
                
                <div className="upload-data w-60">
                    {/* title */}
                    <div className="db event_data">
                        <input Evtype="text" placeholder="Event title!" className="w-60" onChange={e => onChangeTitle(e)} />
                    </div>
                    <div className="db mt4 event_data">
                        <input Evtype="text" placeholder="Event description!" className="w-60" onChange={e => onChangeDesc(e)} />
                    </div>
                    {/* date and time of event */}
                    <div>
                        <Row>
                            <Col span={8} >
                                <dl class="fl fn-l w-50 dib-l w-auto-l lh-title mr5-l">
                                    <dd class="f6 fw4 ml0">Date</dd>
                                    <dd class="f3 fw6 ml0">
                                        <DatePicker
                                            dateRender={current => {
                                                const style = {};
                                                if (current.date() === 1) {
                                                    style.border = '1px solid #1890ff';
                                                    style.borderRadius = '50%';
                                                }
                                                return (
                                                    <div className="ant-picker-cell-inner" style={style}>
                                                        {current.date()}
                                                    </div>
                                                );
                                            }}
                                            onChange={e => onChangeDate(e)}
                                        />
                                    </dd>
                                </dl>
                            </Col>
                            <Col span={8}>
                                <dl class="fl fn-l w-50 dib-l w-auto-l lh-title mr5-l">
                                    <dd class="f6 fw4 ml0">Time</dd>
                                    <dd class="f3 fw6 ml0">
                                        <TimePicker use12Hours format="h:mm a" onChange={onChangeTime} />
                                    </dd>
                                </dl>
                            </Col>
                        </Row>
                    </div>
                    {/* Evtype of event */}
                    <div className="event_Evtype">
                        <div>
                            <h3>Event Type</h3>
                            <div>
                                <Radio.Group onChange={onChangeRadio} value={EvType}>
                                    <Radio value={'birthday'}>Birthday</Radio>
                                    <Radio value={'wedding'}>Wedding</Radio>
                                    <Radio value={'gettogether'}>Get together</Radio>
                                    <Radio value={'vip'}>Vip</Radio>
                                    <Radio value={'others'}>Others</Radio>
                                </Radio.Group>
                            </div>
                        </div>
                    </div>
                    {/* Location of event */}
                    <div className="locevent mt3">
                        <div className="ba b--black-10 br3 w-60">
                            {/* title */}
                            <h3 className="bb b--black-10 pa2 ph2">
                                Event location
                        </h3>
                            {/* map */}
                            {/* location box */}
                            <div className="loca_event">
                                <input onChange={e=>setlocation(e.target.value)} Evtype="text" placeholder="Enter location of event" className="w-100" />
                            </div>

                        </div>
                    </div>

                        <div className="center mt3">
                        <a onClick={onFinish} class="f5 link br-pill bg-light-blue ph3 pv2 w-auto dib fw6 white">Create{load === true? <Spin indicator={antIcon} />:null}</a>
                        </div>
                    </div>
                </div>





            {/* Review section */}
            <div className="w-60 mw5 measure center">
                {/* body */}
                <div className="ba b--black-10 ">
                    <span className="gray ml2 i">up coming {EvType} event &nbsp;
                    </span>
                        <div className="event_im3g">
                        <div className="event_defalut_img">
                            {
                                eventCover.length > 0 ?
                                    <img src={eventCover} alt="" />
                                    : <img src={Picture} alt="" />

                                }

                                <div className="absolute f1 change_event_image gray">

                                    <label className="pointer">
                                        <input type="file" style={{ display: 'none' }} onChange={onChangePicture}/>
                                        <CameraOutlined />
                                    </label>

                                </div>
                            </div>
                            <div className="pa1 ph2">
                                <span>event by,</span>
                                <span>prince randy</span>
                            </div>
                            <div className="">
                                <h2>{title}</h2>

                                <div className="event_info dib bg-near-white pa1 gray">
                                    <p className="i" disabled><InfoCircleOutlined /> {desc}</p>
                                </div>

                                <div className="event_start db">
                                    <Row>
                                        <Col span={8} >
                                            <dl class="fl fn-l w-100 dib-l w-auto-l lh-title">
                                                <dd class="f6 fw4 ml0">Date</dd>
                                                <dd class="f6 fw6 ml0">
                                                    {`"${date}"`}
                                                </dd>
                                            </dl>
                                        </Col>
                                        <Col span={8}>
                                            <dl class="fl fn-l w-100 dib-l w-auto-l lh-title ">
                                                <dd class="f6 fw4 ml0">Time</dd>
                                                <dd class="f5 fw6 ml0">
                                                    {`"${time}"`}
                                                </dd>
                                            </dl>
                                        </Col>
                                    </Row>
                                </div>

                                <div className="event_address db">
                                    <div className="event_con dib">
                                        <span>Location</span>
                                        <span>{location}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* image */}
                {/* user who uploaded */}
                {/* event detail */}
            </div>
            </div>
    )
}
export default TestComponent