import React,{useState} from 'react'
import 'react-phone-number-input/style.css'
import PhoneInput, {isValidPhoneNumber } from 'react-phone-number-input'
import TextField from '@material-ui/core/TextField';
import {
    CameraOutlined,
    SettingOutlined,FlagOutlined,GlobalOutlined,PhoneOutlined
} from '@ant-design/icons'

const Phone = ({myData})=>{
    const [phonevalue, setPhoneValue] = useState('')
    const [website, setWebsite] = useState('')

    const UpdateContact = async()=>{
        const formData = new FormData()
        formData.set('phone',phonevalue)
        formData.set('website',website)
            const setNum= await fetch('http://localhost:4000/Patch/myProfileBio',{
                method:'PATCH',
                headers:{token:localStorage.token},
                body:formData
            })
            const response = await setNum.json()
          
    }
    //set Phone field
    const setPhone = (e) =>{
        setPhoneValue(e)
    }
     //set Phone field
     const setWebsiteAddress = (e) =>{
        setWebsite(e.target.value)
    }
    return (
        <div>
            {/* phone contact */}
            <div class="pa2 ph2 f6">
                <h3 className="f6">Phone contact:</h3>
                <PhoneInput
                    className="pa3 ba b--black-10 br3 w-90 center"
                    international
                    placeholder="Enter phone number"
                    value={phonevalue}
                    onChange={e=>setPhone(e)}
                    error={phonevalue ? (isValidPhoneNumber(phonevalue) ? undefined : 'Invalid phone number') : 'Phone number required'} />
            <span className="gray f6 dim sm db"><PhoneOutlined/> your phone number is {myData.phone}</span>
            </div>

            {/* email`     */}
            <div class="pa2 ph2 f6">
                <h3 className="f6">Email address:</h3>
                <TextField defaultValue={myData.email} id="outlined-basic" label="@email" variant="outlined" className="pa1 ttc f6 w-70" disabled/>
            </div>     

            {/* website */}
            <div class="pa2 ph2 f6">
                <h3 className="f6">website address:</h3>
                <TextField defaultValue={myData.website} onChange={e=>setWebsiteAddress(e)} id="outlined-basic" label="website.com" variant="outlined" className="pa1 ttc f6 w-70" />
            </div>  
            <a class="ml4 f5 link br-pill bg-light-blue ph3 pv2 w-auto dib fw6 white" onClick={UpdateContact}>Save</a>
                    
                   
            
        </div>
    )
}
export default Phone