import React from 'react'
import { Form, Input, Button } from 'antd';
import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';

const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 20 },
    },
  };
  const formItemLayoutWithOutLabel = {
    wrapperCol: {
      xs: { span: 24, offset: 0 },
      sm: { span: 20, offset: 4 },
    },
  };
  
  const DynamicFieldSet = ({upDateChoice}) => {

    const onFinish = values => {
      upDateChoice(values.names)
    };


    return (
        <div className="center">
            <Form name="dynamic_form_item" {...formItemLayoutWithOutLabel} onFinish={onFinish}>
          <Form.List
            name="names"
            rules={[
              {
                validator: async (_, names) => {
                  if (!names || names.length < 2) {
                    return Promise.reject(new Error('At least 2 options'));
                  }
                },
              },
            ]}
          >
            {(fields, { add, remove }, { errors }) => (
              <>
                {fields.map((field, index) => (
                  <Form.Item
                    {...(index === 0 ? formItemLayout : formItemLayoutWithOutLabel)}
                    label={index === 0 ? <></> : ''}
                    required={false}
                    key={field.key}
                  >
                    <Form.Item
                      {...field}
                      validateTrigger={['onChange', 'onBlur']}
                      rules={[
                        {
                          required: true,
                          whitespace: true,
                          message: "Please input option delete this field.",
                        },
                      ]}
                      noStyle
                    >
                      <Input placeholder="option" style={{ width: '80%',borderRadius:'10px' }} />
                    </Form.Item>
                    {fields.length > 1 ? (
                      <MinusCircleOutlined
                        className="dynamic-delete-button"
                        onClick={() => remove(field.name)}
                      />
                    ) : null}
                  </Form.Item>
                ))}
                <Form.Item>
                  <div className="blue f2 pointer">
                  <PlusOutlined 
                    onClick={() => add()}
                />
                <span className="f6">
                Click to add option
                </span>
                  </div>
                 
                  <Form.ErrorList errors={errors} />
                </Form.Item>
                <Form.Item>
                  <Button type="primary" htmlType="submit">done</Button>
                  </Form.Item>
              </>
            )}
          </Form.List>
        </Form>
        </div>
      );

}
export default DynamicFieldSet
