import React from 'react'
import { WalletOutlined,InfoCircleOutlined } from '@ant-design/icons';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import {Alert} from 'antd';

const Paywall =({Paywalldata})=>{

    return(
        <div>
            {
                Paywalldata ?
                    <>
                        {/* image */}
                        <div className="ba b--black-20">
                            <div className="f1 gray hover-blue hover-bg-lightest-blue  w-30 dib">
                                <WalletOutlined />
                            </div>
                            <div className="dib">
                                <span className="b f5 ttc">your paywall balance , </span>
                                <span className="flex f5">
                                    <AttachMoneyIcon />{Paywalldata.balance}
                </span>
                            </div>
                        </div>
                        {/* side amount */}
                        {/* initialize withdrawal */}
                        <div className="mt2">
                            <code className="dib tc blue bg-lightest-blue hover-bg-light-blue br-pill w-25 ml4 pa2 ph2 pointer">withdraw</code>
                            <a href="/palscheck/payment" className="dib tc blue bg-lightest-blue hover-bg-light-blue br-pill w-25 ml4 pa2 ph2 pointer">top up</a>
                        </div>
                        {/* details. */}
                        <p className="f6 gray">
                            <span className="pa1"><InfoCircleOutlined />
            your paywall lets you earn from palscheck, money in this wallet can also be use to manage your palscheck account <a className="link light-blue" href="https://help.palscheck.com">learn more about paywall</a>
                            </span>
                        </p>
                        {/* additional info */}

                        {/* set Account */}
                    </>
                    :
                <p>
                    <Alert
                    type="info"
                    message={"No data"}
                    description={`your paywall is not yet activated`}
                    showIcon
                    />
                </p>
            }
            
        </div>
    )
}
export default Paywall