import React from 'react'

const Personalize = ({theme}) => {
    return (
        <div className="flex">
            <div className="w-40 br b--black-10">
                <li className="bb b--black-10 pa2 tj list link f5 pointer black">change theme</li>
             </div>
            <div className="w-60 bl b--black-10">
               <h4 className="ml1">Themes</h4>
                <div className="flex">
                    <div className="f5 pointer" onClick={e=>theme('dark')}>
                    <span>Dark theme</span>
                    </div>
                    <div className="f5 ml4 pointer" onClick={e=>theme('light')}>
                    <span>light theme</span>
                    </div>
                </div>
            </div>
        </div>

    )
}
export default Personalize