import React from 'react'
import { Select,DatePicker } from 'antd';
import TextField from '@material-ui/core/TextField';
import 'date-fns';
import { Spin,Alert } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
const { Option } = Select;
const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;


const AddBankDetails = () =>{
const [selectedDate, setSelectedDate] = React.useState(new Date('2014-08-18T21:11:54'));
const [Bankname, setBankname] = React.useState('');
const [Accountname, setAccountname] = React.useState('');
const [Accountnum, setAccountnum] = React.useState('');
const [secret1, setsecret1] = React.useState('');
const [secretans1, setsecretans1] = React.useState('');
const [IDnum, setIDnum] = React.useState('');
const [load,setload] = React.useState(false)
const [error,setError] = React.useState('')
const [success,setSuccess] = React.useState('')

function onChangeDate(date, dateString) {
    setSelectedDate(dateString)
  }

  const onFinish = async() =>{
    setload(true)
    const senData = await fetch('http://localhost:4000/Patch/updatePAywallData',{
        method:'PATCH',
        headers:{"Content-Type":"application/json",token:localStorage.token},
        body:JSON.stringify({
            bname:Bankname,
            actname:Accountname,
            qes1:secret1,
            ans1:secretans1,
            DOB:selectedDate,
            IDnum:IDnum
        })
    })
    const res = await senData.json()
    if(res.error){
        setError(res.error)
        setload(false)
    }else if(res.data){
        setError('')
        setSuccess('your data has been updated successfully')
        setload(false)
    }
  }
    return(
        <div className="pa1">
            <h3 className="f6 blue">Make sure to fill your legal info</h3>
            <span className="f6 gray dim">as this will be use to process your transactions on palscheck</span>

            {/* form */}
            <div>   
                {/* bankname */}
                <div className="mt2 pa1 center">
                <TextField onChange={e=>setBankname(e.target.value)} value={Bankname}  className="w-80" id="outlined-basic" label="Bank name" variant="outlined" />
                </div>
                
                {/* acct name */}
                <div className="mt2 pa1 center">
                <TextField onChange={e=>setAccountname(e.target.value)} value={Accountname} className="w-80" id="outlined-basic" label="Account name" variant="outlined" />
                </div>
                
                {/* acct number */}
                <div className="mt2 pa1 center">
                <TextField onChange={e=>setAccountnum(e.target.value)} value={Accountnum} className="w-80" id="outlined-basic" label="Account number" variant="outlined" />
                </div>

                {/* dob */}
                <div className="mt2 pa1 center">
                D.O.B:<DatePicker onChange={onChangeDate} />
                </div>{/* acct number */}          
                {/* secret */}
                <div className="mt2 pa1 center">
                <Select
                            style={{ width: '80%' }}
                            placeholder="Select secret"
                            optionFilterProp="children"
                            onChange={e=>setsecret1(e)}
                            filterOption={(input, option) =>
                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                            }
                        >
                            <Option value="what city did you live in first">what city did you live in first</Option>
                            <Option value="what was your childhood pet nam">what was your childhood pet name</Option>
                            <Option value="how old where you when your first locatio">how old where you when your first location</Option>
                            <Option value="where did you first start your carree">where did you first start your carree</Option>
                            <Option value="where did you spend your honeymoon">where did you spend your honeymoon</Option>
                         </Select>
                <TextField onChange={e=>setsecretans1(e.target.value)} value={secretans1} className="w-80" id="outlined-basic" label="answer" variant="outlined" />
                </div>
                
                {/* ID number */}
                <div className="mt2 pa1 center">
                <TextField onChange={e=>setIDnum(e.target.value)} value={IDnum} className="w-80" id="outlined-basic" label="ID number" variant="outlined" />
                <span className="sm dim blue db" style={{fontSize:'12px'}}>should ba same as the one you submitted for approval.</span>
                </div>

                {
                            error.length > 0?
                            <Alert message={error} type="error" showIcon />
                            :null
                        }
                        {
                            success.length > 0?
                            <Alert message={success} type="success" showIcon />
                            :null
                        }
                        <a class="ml4 mt1 f5 link br-pill bg-light-blue ph3 pv2 w-30 dib fw6 white dib tc" onClick={onFinish}>Done {load === true? <Spin indicator={antIcon} />:null}</a>
            </div>

        </div>
    )
}
export default AddBankDetails