import {
    CameraOutlined,
    SettingOutlined,FlagOutlined,GlobalOutlined,UserOutlined
} from '@ant-design/icons'
import { CountryDropdown, RegionDropdown } from 'react-country-region-selector';
import { TextareaAutosize } from '@material-ui/core'
import TextField from '@material-ui/core/TextField';
import React from 'react'
import CancelIcon from '@material-ui/icons/Cancel';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';

const MyProfilebio = ({myData}) => {
    const [country, setCountry] = React.useState('')
    const [names, setNames] = React.useState('')
    const [Username, setUserName] = React.useState('')
    const [bio, setUserbio] = React.useState('')
    const [region, setRegion] = React.useState('')
    const [address, setUserAddress] = React.useState('')
    const [CoverPicture, setPicture] = React.useState('https://cdn.wallpapersafari.com/25/14/Dljuh1.jpg');
    const [eventCover, setImgData] = React.useState([]);
    const [ProfilePicture, setPicture1] = React.useState('https://cdn.wallpapersafari.com/25/14/Dljuh1.jpg');
    const [eventProfile, setImgData1] = React.useState([]);
    const [error, setError] = React.useState('')
    const [success, setSuccess] = React.useState(0)

    //changing profile cover art
    const onChangePictureCover = e => {
        if (e.target.files[0]) {
            setPicture(e.target.files[0]);
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                setImgData(reader.result);
            });
            reader.readAsDataURL(e.target.files[0]);
        }
    };

    //changing profile profile art
    const onChangePictureProfile = e => {
        if (e.target.files[0]) {
            setPicture1(e.target.files[0]);
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                setImgData1(reader.result);
            });
            reader.readAsDataURL(e.target.files[0]);
        }
    };

    //set Country
    const SetcountryField = (e) => {
        setCountry(e)
    }
    //set Region
    const SetregionField = (e) => {
        setRegion(e)
    }

    //onFinish
    const onFinish = async () => {
        const formData = new FormData()
        formData.set('names', names)
        formData.set('country', country)
        formData.set('region', region)
        formData.set('bio', bio)
        formData.set('username', Username)
        formData.set('address', address)
        
        const UpdateData = await fetch('http://localhost:4000/Patch/myProfileBio', {
            method: 'PATCH',
            headers: { token: localStorage.token },
            body: formData
        })

        const response = await UpdateData.json()
        if (response.error) {
            setError(response.error)
            setSuccess(0)
        } else {
            setSuccess(1)
            setError('')
        }
    }

    const setProfileIMg = async()=>{
        const formData = new FormData()
        formData.append('file', ProfilePicture);
        const UpdateData = await fetch('http://localhost:4000/Patch/myProfileImg', {
            method: 'PATCH',
            headers: { token: localStorage.token },
            body: formData
        })
        const response = await UpdateData.json()
        if (response.error) {
            setError(response.error)
            setSuccess(0)
        } else {
            setSuccess(1)
            setError('')
        }
    }
    const setCoverIMg = async()=>{
        const formData = new FormData()
        formData.append('file', CoverPicture);
        const UpdateData = await fetch('http://localhost:4000/Patch/myCoverImg', {
            method: 'PATCH',
            headers: { token: localStorage.token },
            body: formData
        })
        const response = await UpdateData.json()
        if (response.error) {
            setError(response.error)
            setSuccess(0)
        } else {
            setSuccess(1)
            setError('')
        }
    }

    const clearCoverImg = ()=>{
        setPicture(null);
        setImgData([])
    
    }
    const clearProfileIMg = ()=>{
        setPicture1(null);
        setImgData1([])
    }
    return (
        <div>
            <h4 className="ml1 dib">my profile</h4>
            <h4 className="ml1 dib tr ml5"><SettingOutlined /> General</h4>

            <div className="edit_profile">
                {/* profile image and cover */}
                <div className="edit_profile_img">
                    <div className="w-100 pa2 ph2 br3 center edit_cover_img ba b--blue">
                        <div className="editinnerCoverImage">
                            <img src={
                                myData.coverimg?
                                myData.coverimg:
                                eventCover
                                } alt="" />
                        </div>
                        <label className="f3 pointer">
                            <input type="file" style={{ display: 'none' }} onChange={onChangePictureCover} />
                            <CameraOutlined />
                        </label>
                        {
                            eventCover.length >0 ?
                                <div>
                                    <code className="tl f3 blue fw1 pointer flex" onClick={setCoverIMg}><CheckCircleOutlineIcon/><span className="f6 ml1">save</span></code>
                                    <code className="tr f3 blue absolute pcc pointer" onClick={clearCoverImg}><CancelIcon/></code>
                                </div>
                                : null
                        }
                    </div>
                    <div className="w-50 pa2 ph2 br3 center edit_profile_img ba b--blue relative">
                        <div className="editinnerProfileImage">
                            <img src={
                                myData.profileimg?
                                myData.profileimg:
                                eventProfile
                                } alt="" />
                        </div>
                        <label className="f3 pointer">
                            <input type="file" style={{ display: 'none' }} onChange={onChangePictureProfile} />
                            <CameraOutlined />                            
                        </label>

                        {
                            eventProfile.length>0?
                                <div>
                                    <code className="tl f3 blue fw1 pointer flex" onClick={setProfileIMg}><CheckCircleOutlineIcon/><span className="f6 ml1">save</span></code>
                                    <code className="tr f3 blue fw1 absolute ppc pointer" onClick={clearProfileIMg}><CancelIcon/></code>
                                </div>
                                : null
                        }
                    </div>
                </div>
                {/* profile data */}
                <div className="pals_edit_data pa2 ">
                    {/* names */}
                    <h3>Edit profile names</h3>
                    <div className="pa2 mt1">
                        <TextField defaultValue={myData.fullname} onChange={e => setNames(e.target.value)} id="outlined-basic" label="names" variant="outlined" className="pa1 ttc f6 w-70" />
                    </div>
                    {/* username */}
                    <div className="pa2 mt1">
                        <TextField onChange={e => setUserName(e.target.value)} defaultValue={myData.username} id="outlined-basic" label="username" variant="outlined" className="pa1 ttc f6 w-70" />
                        <span className="gray f6 dim sm db"><UserOutlined/> your username is {myData.username}</span>
                    </div>
                    {/* bio */}
                    <h3>Edit profile bio</h3>
                    <div className="pa2 mt1">
                        <TextareaAutosize rowsMax={80}
                            onChange={e => setUserbio(e.target.value)} 
                            aria-label="maximum height"
                            placeholder="Bio"
                            defaultValue={myData.about}
                            className="pa1 f6 w-70 br3 ba b--blue black" />
                    </div>
                    {/* location */}
                    {/* country */}
                    <div className="location_user">
                        <h3>Location</h3>
                        <span className="db">country: <CountryDropdown style={{ border: "1px solid #357edd", padding: "10px" }} value={country} onChange={e => SetcountryField(e)} /></span>
                        <span className="gray f6 dim sm db"><FlagOutlined/> your current country is {myData.country}</span>
                        <span className="db mt2 pa3">state: <RegionDropdown
                            country={country}
                            value={region}
                            onChange={e => SetregionField(e)}
                            style={{ border: "1px solid #357edd", padding: "10px" }}
                        />
                        </span>
                        <span className="gray f6 dim sm db"><GlobalOutlined/> your current region is {myData.region}</span>
                        <div className="pa2 mt1">
                            Address: <TextField onChange={e => setUserAddress(e.target.value)} defaultValue={myData.address} id="outlined-basic" label="address" variant="outlined" className="pa1 ttc f6 w-70" />
                            <span className="gray f6 dim sm db"><GlobalOutlined/> your current address is {myData.address}</span>
                        </div>
                    </div>
                </div>
                <span className="red">{error}</span>
                {
                    success === 1 ?
                        <a class="ml4 f5 link br-pill bg-light-blue ph3 pv2 w-auto dib fw6 white" onClick={onFinish}>saved</a>
                        :
                        <a class="ml4 f5 link br-pill bg-light-blue ph3 pv2 w-auto dib fw6 white" onClick={onFinish}>Save</a>
                }

            </div>
        </div>

    )
}
export default MyProfilebio