import React from 'react'
import { Carousel, Avatar, Spin ,Menu, Dropdown,Image} from 'antd';
import '../newsfeed.css'
import { Row, Col } from 'antd';
import {LoadingOutlined,EllipsisOutlined,CheckCircleFilled} from '@ant-design/icons';
import Comments from '../Comment/Comments.jsx'
import Reaction from '../Reaction/reaction.jsx'
import BookMarkbtn from '../Bookmark/bookmarkbtn.jsx'
import Talkbtn from '../../Talks/TalkStack/joinTalk.jsx'
import Followbtn from '../../Follow/feedFollowbtn'
import DeleteFeed from '../deleteFeed'
import ReadMoreReact from 'read-more-react';
import { Player, ControlBar, PlayToggle } from 'video-react';
import DefaultImage from '../../Dashboard/Defaults/defaultImage'
import RandomString from 'randomstring'
import EditFeed from '../editFeed'
import ReportContent from '../reportFeed'

const Randstring  = RandomString.generate({
  length:50
})
const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;
const style = { background: '', padding: '8px 0' };
const NewsFeedView = ({feedby,address,loggedIn,id,feedFor,feedType,UserDetails,verified,FeedMedia,contentStyle,feedTxt,date}) =>{
    console.log(UserDetails)
    const menu = (
        <Menu>
          <Menu.Item key="0">
            {
              loggedIn !== feedby ?
                <Followbtn userid={feedby} followtype={'people'} name={UserDetails.fullname}/>
                : null
            }
            
          </Menu.Item>
          <Menu.Item key="2">
          {
              loggedIn === feedby ?
              <DeleteFeed address={id} posteby={feedby}/>
                : null
            }
            
          </Menu.Item>
          <Menu.Item key="3">
            <ReportContent/>
          </Menu.Item>
        </Menu>
      );
      const readMore = (
        <span class=" black-60 underline-hover pointer f6">
        see more...
      </span>
      )
    return(
        <div className="feed-content mt4">
        {/* profile image */}
        
        {
          UserDetails.profileimg ?
            <Avatar src={UserDetails.profileimg} style={{ float: 'left', marginRight: '15px' }} size={40} />
          :<DefaultImage name={UserDetails.fullname} size={40}/>
        }
           
         
          <div className="right-side pa2"> 
          {/* profile name */}
          <div className="tl">
            <a style={{color:'inherit'}} href={`${UserDetails.username}.pal`}><span className="ttc fw6 feedname ">{UserDetails.fullname}</span></a>
            {
              UserDetails.verified === 1?
              <span className="blue ml2"><CheckCircleFilled /></span>
              :null
            }
            
              <span className="gray ml1 f6">@{UserDetails.username}</span>
              
          </div>
             {/* content text */}
             <a className="">
             <div className="tl mt3">
              {
                feedTxt ? <p className="feedtxt br3 ba b--black-10 pa2 "><ReadMoreReact text={feedTxt}
                min={80}
                ideal={90}
                max={1000}
                  readMoreText={readMore} /></p> : null
              }


              {/* content image */}
              <div className="feed-images pointer w-100"  style={{overflow:"hidden"}}>
              
                         {
                        FeedMedia?
                          FeedMedia.feedMedia_type === "image/jpeg" || FeedMedia.feedMedia_type === "image/jpg" || FeedMedia.feedMedia_type === "image/png"?
                           <Image
                            style={contentStyle}
                            src={FeedMedia.url}
                        />
                           : FeedMedia.feedMedia_type === "video/mp4"?
                            <Player
                           poster="/assets/poster.png"
                           style={contentStyle}
                           src={FeedMedia.url}
                         >
                           <ControlBar autoHide={false} disableDefaultControls={true}>
                             <PlayToggle />
                           </ControlBar>
                         </Player>
                           :null
                           :null
                         }
              </div>
             </div>
             </a>
              {/* comment like icon */} 
              <div className="commetLike mt4">
              <Row gutter={16}>
                  <Col className="gutter-row f4 feed-c-i pointer" span={6}>
                      <Reaction
                      feed_id={id}
                      feed_by={feedby}
                      />
                  </Col>
                  <Col className="gutter-row f4 feed-c-i pointer" span={6}>
                  <Talkbtn
                    feed_id={id}   
                    feed_by={feedby}
                   title= {feedTxt?feedTxt: Randstring}                
                    />
                  </Col>
                  <Col className="gutter-row f4 feed-c-i pointer" span={6}>
                    <BookMarkbtn
                    />
                  </Col>
              </Row>
              </div>

      </div>
      </div>
    )
}
export default NewsFeedView