import React from 'react'
import SingleFeedTemp from './singleFeedTemp'
import MetaHead from '../../Helmet/helment'
class SingleFeed extends React.Component{

    render(){
        
        return(
            <>
            <MetaHead title={`palscheck | ${this.props.match.params.title}`} description={this.props.match.params.title}/>
            <div className="flex justify-center">
            
            {/* profileNewsfeed */}
            <article className="newfeed--3-art pa3 pa4-ns ba b--black-10 w-60">
                <SingleFeedTemp
                address={this.props.match.params.id}
                />
            </article> 
        </div>
            </>
        )
    }
}
export default SingleFeed